      }
    }
  }
  .el-main {
    padding: 0px 10px 0 20px;
    .el-card {
      .result-header {
        font-size: 25px;
      }
      .el-table {
        margin-top: -20px;
        height: 70vh;
        overflow: scroll;
      }
      .el-button {
        margin-top: 20px;
      }
    }
  }
}
